import React from 'react';


const TableHeader =()=> {
    return (
            <thead className="table_head">
                <tr>
                    <th>Case</th>
                    <th>Deadline</th>
                    <th>Remove</th>
                </tr>
            </thead>
    )
}
const TableBody =(props)=> {
    const {caseData,removeCase} = props
    
    const row = caseData.map((data, index)=> {
        return (
            <tr key={index}>
                <td>{data.nameCase}</td>
                <td>{data.deadline}</td>
                <td>
                    <button onClick={()=>removeCase(index)} className="btn_remove">Удалить</button>
                </td>
            </tr>
        )
    })

    return (
        <tbody> {row} </tbody>
    )
}

const Table = (props)=> {
    const {caseData, removeCase} = props

    return (
        <table className="tbl">
            <TableHeader />
            <TableBody 
                caseData={caseData}
                removeCase={removeCase}
            />
        </table>
    )
}

export default Table

