import React, {Component} from 'react';

export default class Form extends Component {
    constructor(props) {
        super(props);

        this.initialState = {
            nameCase: '',
            deadline: ''
        }

        this.state = this.initialState;
    }
    
    formSubmitHandler =(event)=> {
        event.preventDefault();
        const {nameCase, deadline} = this.state;

        if  (/[\wа-я]{3}/i.test(nameCase) && /[\wа-я]{3}/i.test(deadline)) {
            this.props.formHandler(this.state)
            this.setState(this.initialState)
        }
    }


    changeHandle =(event)=> {
        const {name, value} = event.target
        
        this.setState({
            [name]: value
        })        
    }

    render() {

        const {nameCase, deadline} = this.state;

        return(
            <form onSubmit={this.formSubmitHandler} className="form_class">

                <label htmlFor="case">Case</label>
                <input 
                id="case"
                type="text" 
                name="nameCase"
                value={nameCase}
                onChange={this.changeHandle}
                />

                <label htmlFor="case">Deadline</label>
                <input 
                id="deadline"
                type="text"
                name="deadline"
                value={deadline} 
                onChange={this.changeHandle}
                />
               <button type="submit" className="btn_submit">SUBMIT CASE</button>
            </form>
        )
    }
}