import React, {Component} from 'react';
import Table from './table/table';
import Form from './table/form';

class App extends Component {
 
  state = {
    cases: [
      {
        nameCase: 'Купить хлеб',
        deadline: 'сегодня'
      },
      {
        nameCase: 'Погулять с Лёвой',
        deadline: 'каждый день'
      },
    ]
  }

  removeCase = index => {
    const {cases} = this.state;

    this.setState({
      cases: cases.filter((_,idx)=> idx !== index)
    })
  }

  formHandler = nameCase => {
    const {cases} = this.state;
    this.setState({cases: [...cases, nameCase]})
  }

  render() {
    const {cases} = this.state;

    return (
      <div className="container">        
        <h1>Список дел</h1> 
        
        <Table
          caseData={cases}
          removeCase={this.removeCase}          
        /> 
        <Form formHandler={this.formHandler}/>       
      </div>
    )
 }
}

export default App;
